import { h } from "@stencil/core";
export class RaulStepTracker {
    render() {
        return (h("div", { class: "r-step-tracker" },
            h("slot", null)));
    }
    static get is() { return "raul-step-tracker"; }
    static get originalStyleUrls() { return {
        "$": ["raul-step-tracker.scss"]
    }; }
    static get styleUrls() { return {
        "$": ["raul-step-tracker.css"]
    }; }
}
