raul-step-tracker {
  padding: 1.5rem;
  background-color: #F7F8F9;
  width: 100%;
  display: inline-table;
}

raul-step-tracker .r-step-tracker {
  display: flex;
}

raul-step-tracker .r-step-tracker .r-step:not(:first-child) {
  margin-left: 2px;
}

raul-step-tracker .r-step-tracker .r-step:first-child {
  border-top-left-radius: 0.125rem;
  border-bottom-left-radius: 0.125rem;
  padding-left: 2rem;
}

raul-step-tracker .r-step-tracker .r-step:first-child:before {
  content: unset;
}

raul-step-tracker .r-step-tracker .r-step:last-child {
  border-top-right-radius: 0.125rem;
  border-bottom-right-radius: 0.125rem;
  padding-right: 2rem;
}

raul-step-tracker .r-step-tracker .r-step:last-child:after {
  content: unset;
}

@media (max-width: 640px) {
  raul-step-tracker {
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
  }

  raul-step-tracker .r-step-tracker {
    margin-left: -12px;
  }

  raul-step-tracker .r-step-tracker .r-step:not(.r-step--current) {
    display: none;
  }

  raul-step-tracker .r-step-tracker .r-step--current {
    border-top-left-radius: 0.125rem;
    border-bottom-left-radius: 0.125rem;
  }

  raul-step-tracker .r-step-tracker .r-step--current:before {
    content: unset;
  }
}